<template>
  <div>
    <v-navigation-drawer
      width="200"
      v-model="drawer"
      :permanent="navigationDrawerState"
      :expand-on-hover="isExpanded"
      dark
      fixed
    >
      <template v-slot:prepend>
        <v-list-item class="px-2 logo-item">
          <v-list-item-avatar class="logo-min">
            <v-img src="@/assets/images/logo.svg"></v-img>
          </v-list-item-avatar>
          <v-list-item-icon class="logo-extended">
            <v-img src="@/assets/images/logo-full.svg"></v-img>
          </v-list-item-icon>
        </v-list-item>
      </template>

      <v-list
        nav
        dense
      >
        <v-list-item
          v-for="menuItem in menuItems"
          :key="menuItem.title"
          link
          :to="menuItem.route"
        >
          <v-list-item-icon>
            <v-icon>{{ menuItem.icon }}</v-icon>
          </v-list-item-icon>
          <v-list-item-title v-once>{{ menuItem.title }}</v-list-item-title>
        </v-list-item>
      </v-list>

      <template v-slot:append>
        <v-list
          nav
          dense
        >
          <v-list-item link>
            <v-list-item-icon>
              <v-icon>mdi-cellphone-iphone</v-icon>
            </v-list-item-icon>
            <v-list-item-title>{{ $t("sidebar.mobileVersion") }}</v-list-item-title>
          </v-list-item>
          <v-list-item link>
            <v-list-item-icon>
              <v-icon>mdi-account-circle</v-icon>
            </v-list-item-icon>
            <v-list-item-title>{{ $t("sidebar.userAccount") }}</v-list-item-title>
          </v-list-item>
          <v-list-item link @click="logout">
            <v-list-item-icon>
              <v-icon>mdi-logout</v-icon>
            </v-list-item-icon>
            <v-list-item-title>{{ $t("sidebar.logout") }}</v-list-item-title>
          </v-list-item>
        </v-list>
      </template>
    </v-navigation-drawer>
    <v-toolbar color="white" class="hidden-sm-and-up" width="100%">
      <v-app-bar-nav-icon
        @click.stop="drawer = !drawer"
      ></v-app-bar-nav-icon>
    </v-toolbar>
  </div>
</template>

<script>
export default {
  name: 'SidebarLayout',
  data () {
    return {
      drawer: false,
      isExpanded: false,
      menuItems: [
        {
          icon: 'mdi-calendar-month',
          title: this.$t('sidebar.calendar'),
          route: '/calendar'
        },
        {
          icon: 'mdi-pencil-box',
          title: this.$t('sidebar.booking'),
          route: '/booking'
        },
        {
          icon: 'mdi-cash',
          title: this.$t('sidebar.feesPoints'),
          route: '/fees-points'
        },
        {
          icon: 'mdi-vector-polyline mdi-rotate-90',
          title: this.$t('sidebar.externalCooperation'),
          route: '/external-cooperation'
        },
        {
          icon: 'mdi-desktop-mac-dashboard',
          title: this.$t('sidebar.report'),
          route: '/report'
        },
        {
          icon: 'mdi-hospital-building',
          title: this.$t('sidebar.facility'),
          route: '/facility'
        },
        {
          icon: 'mdi-printer-settings',
          title: this.$t('sidebar.printingForm'),
          route: '/printing-form'
        }
      ]
    }
  },
  emits: {
    'is-sidebar-open': null
  },
  mounted () {
    this.isExpanded = this.navigationDrawerState
  },
  watch: {
    navigationDrawerState (state) {
      this.drawer = state
      this.isExpanded = state
    },
    isExpanded (value) {
      this.$emit('is-sidebar-open', value)
    }
  },
  computed: {
    navigationDrawerState () {
      return ['sm', 'md', 'lg', 'xl'].includes(this.$vuetify.breakpoint.name)
    }
  },
  methods: {
    async logout () {
      if (await this.$store.dispatch('LogOut')) {
        console.log('logged out')
        await this.$router.push({ path: '/signin' })
      }
    }
  }
}
</script>

<style scoped lang="scss">
  .v-navigation-drawer.v-navigation-drawer--mini-variant {

  }

  .v-navigation-drawer {
    background-color: #2173d9!important;

    .logo-extended {
      display: none;
      width: 110px!important;
      margin-left: 0!important;
    }

    .logo-min {
      display: block;
    }

    &.v-navigation-drawer--is-mouseover, &:not(.v-navigation-drawer--close):not(.v-navigation-drawer--mini-variant) {
       //background-color: rgb(29, 79, 135)!important;

      .logo-min {
        display: none;
      }

      .logo-extended {
        display: block;
      }

    }

    .v-list-item__icon {
      margin-right: 15px!important;
    }
  }

  .v-list-item {
    &.logo-item {
      height: 73px;
    }

    &.v-list-item__icon {
      background-size: contain;
      width: 24px;
      margin-right: 12px!important;
    }
  }
</style>
