<template>
  <v-main>
    <SidebarLayout @is-sidebar-open="onSidebarStateChange"></SidebarLayout>
    <div class="main-layout-container" :class="{ 'sidebar-open' : isSidebarOpen }">
      <slot />
    </div>
  </v-main>
</template>

<script>
import SidebarLayout from '@/layouts/SidebarLayout'
export default {
  name: 'MainLayout',
  components: { SidebarLayout },
  data: () => {
    return {
      isSidebarOpen: false
    }
  },
  methods: {
    onSidebarStateChange (value) {
      this.isSidebarOpen = value
    }
  }
}
</script>

<style scoped>
  .main-layout-container.sidebar-open {
    width: calc(100% - 56px);
    margin-left: 56px;
  }
</style>
