export default new Vuetify({
  theme: {
    themes: {
      light: {
        primary: '#1873D9',
        secondary: '#4798f2',
        errorButton: '#DB3069'
      }
    }
  },
  icons: {
    iconfont: 'mdiSvg' // 'mdi' || 'mdiSvg' || 'md' || 'fa' || 'fa4' || 'faSvg'
  }
})
