import request from '@/utils/request'

export function loginByUsername (username, password) {
  const data = {
    username,
    password
  }
  return request({
    url: '/ce2f9c4e-07b4-4ac7-8457-eb91741c2cca',
    method: 'post',
    data
  })
}

export function logout () {
  return request({
    url: '/966e8c77-d6e8-4fe9-b4a7-c5385b77f2d7',
    method: 'get'
  })
}

export function getUserInfo (token) {
  return request({
    url: '/ce2f9c4e-07b4-4ac7-8457-eb91741c2cca',
    method: 'get',
    params: { token }
  })
}
