/** When your routing table is too long, you can split it into small modules* */

import store from '@/store'
import DashboardPage from '@/views/dashboard/dashboard.vue'
import DashboardComponent from '@/components/DashboardComponent'
import { i18n } from '@/plugins/i18n'

const dashboardRouter = {
  path: '',
  // component: {
  //   // Inline declaration of a component that renders our <router-view>
  //   render: (c) => c('router-view')
  // },
  component: DashboardComponent,
  beforeEnter (to, from, next) {
    console.log('store', store.state.user.token)
    if (store.state.user.token) {
      console.log('going to next')
      next()
    } else {
      console.log('going to sign')
      next('/signin')
    }
  },
  children: [
    { path: '/', redirect: '/dashboard' },
    {
      path: 'dashboard',
      name: i18n.t('routeTitles.dashboard'),
      component: DashboardPage
    },
    {
      path: '/calendar',
      name: i18n.t('routeTitles.calendar'),
      component: () => import('@/views/dashboard/calendar')
    },
    {
      path: '/booking',
      name: i18n.t('routeTitles.booking'),
      component: () => import('@/views/dashboard/booking')
    },
    {
      path: '/fees-points',
      name: i18n.t('routeTitles.feesPoints'),
      component: () => import('@/views/dashboard/feesPoints')
    },
    {
      path: '/external-cooperation',
      name: i18n.t('routeTitles.externalCooperation'),
      component: () => import('@/views/dashboard/externalCooperation')
    },
    {
      path: '/report',
      name: i18n.t('routeTitles.report'),
      component: () => import('@/views/dashboard/report')
    },
    {
      path: '/facility',
      redirect: '/facility/settings'
    },
    {
      path: '/facility',
      component: {
        render: (c) => c('router-view')
      },
      children: [
        {
          path: 'settings',
          component: { render: (c) => c('router-view') },
          children: [
            {
              path: '',
              name: i18n.t('routeTitles.facility'),
              component: () => import('@/views/dashboard/facilities/facilitySettings/settings')
            },
            {
              path: ':facilityId/edit',
              name: i18n.t('routeTitles.editingFacility'),
              component: () => import('@/views/dashboard/facilities/facilitySettings/settings/edit')
            },
            {
              path: ':facilityId/room-types',
              name: i18n.t('routeTitles.roomTypesSettings'),
              component: () => import('@/views/dashboard/facilities/facilitySettings/roomTypes/settings')
            },
            {
              path: ':facilityId/room-types/:roomTypeId/edit',
              name: i18n.t('routeTitles.roomTypeEditing'),
              component: () => import('@/views/dashboard/facilities/facilitySettings/roomTypes/edit')
            },
            {
              path: ':facilityId/room-types/:roomTypeId/rooms',
              name: i18n.t('routeTitles.roomsSettings'),
              component: () => import('@/views/dashboard/facilities/facilitySettings/roomTypes/rooms/settings')
            },
            {
              path: ':facilityId/room-types/:roomTypeId/rooms/:roomId/edit',
              name: i18n.t('routeTitles.roomsEditing'),
              component: () => import('@/views/dashboard/facilities/facilitySettings/roomTypes/rooms/edit')
            }
          ]
        }
      ]
    },
    {
      path: '/facility/loans',
      name: i18n.t('routeTitles.loans'),
      component: () => import('@/views/dashboard/facilities/loans/loans')
    },
    {
      path: '/facility/key-settings',
      name: i18n.t('routeTitles.keySettings'),
      component: () => import('@/views/dashboard/facilities/keySettings/keySettings')
    },
    {
      path: '/printing-form',
      name: i18n.t('routeTitles.printingForm'),
      component: () => import('@/views/dashboard/printingForm')
    }
  ]
}

export default dashboardRouter
